// _config.scss
$font-stack: Arial, Helvetica, sans-serif;
$docs-font: Didot, serif;
$background-color: yellow;
$button-color: #ffc107;
$navbar-color: #f2e824;
$text-color: #000;
$link-text: DodgerBlue;


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: $background-color;
  text-align: center;
  font-family: $font-stack;
  line-height: 1.6;
}

a {
  text-decoration: none;
  color: $link-text;
}

a:hover {
  text-decoration: none;
  color: $text-color;
}

%btn {
  display: inline-block;
  margin: 15px;
  color: $text-color;
  font-size: 18px;
  padding: 9px 15px;
  border-radius: 20px;
  background-color: $button-color;
  border: 3px;
  border-style: outset;

  &:hover {
    transform: scale(.98);
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  }
}

.butts{
  @extend %btn;
}


.butts-small{
  @extend %btn;
  font-size: 18px;
  padding: 10px;
  border-radius: 15px;
  margin: 10px;
}

.butts-nav{
  @extend %btn;
  font-size: 14px;
  padding: 5px;
  border-radius: 10px;
  margin: 0;
}

.button-group{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.docs{
   text-align: left;
   font-family: $docs-font;
}

.results{
  border: 4px outset $button-color;
}


nav {
  display: flex;
  align-items: right;
  justify-content: space-between;
  // background-color:$navbar-color;
  }

  ul {
      display: flex;
    list-style-type: none;
  }

  li {
    padding: 15px;
  }
